import tips from '../../element/tips.vue'	
import { API } from '../../api/request.js';
const api = new API();
export default {
	components:{
		tips
	},
	data() {
		return {
			searchdata: '', //搜索
			takeshop: [], //商品
			setdata: [], //历史记录
		};
	},
	methods: {
		// 搜索
		seArch(e) {
			if(this.searchdata==='') return
			this.postsearchurl();
			this.historySeach()
		},
		// 搜索接口
		postsearchurl() {
			const param = { searchdata: this.searchdata };
			api.postsearchurl(param)
				.then(res => {
					this.setdata=[]
					console.log(res, '搜索到的商品');
					if (typeof res.data === 'string') {
						this.takeshop = [];
						this.onTips(true,"没有商品")
					} else {
						this.takeshop = res.data;
						this.onTips(false,"")
					}
				})
				.catch(err => {
					console.log('获取数据失败');
				});
		},
		// 存储历史记录
		historySeach() {
			const data =uni.getStorageSync('setdata')||[]
			data.unshift(this.searchdata);
			uni.setStorageSync('setdata', Array.from(new Set(data)));
		},
		// 清空历史记录
		removeStorage(){
			this.setdata=[]
			uni.removeStorageSync('setdata')
		},
		// 点击搜索历史记录进行搜索
		menubtn(item){
			this.searchdata=item
			this.seArch()
		},
		// 获取搜索历史记录
		getHistory(){
			this.setdata=uni.getStorageSync('setdata')||[]
		},
		onTips(bull,tips){
			// 页面渲染完后进行调用
			this.$nextTick(()=>{
				this.$refs.tip.init(bull,tips)
			})
		}
		
	},
	
	created() {
		this.getHistory()
	}
	
};
